.grid {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(12rem, 16rem));
  row-gap: 2rem;
  column-gap: 0.5rem;
  justify-content: center;
  /* justify-items: center; */
}

.loading {
  color: white;
  text-align: center;
}

.no-results {
  color: white;
  text-align: center;
}
